import axios from 'axios';
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { setNotification } from '../feature/notification.slice';
import Loader from './Loader';


const UserCard = (props: any) => {
    let { firstname, lastname, email, speciality, contact, image, _id } = props.user;
    let { setRefrech } = props;

    const { userId } = useSelector((store: any) => store.session)
    const [loaded, setLoaded] = useState(false)
    const [isEdit, setIsEdit] = useState(false)
    const dispatch = useDispatch()

    const uploadImage = (ev: any): void => {
        setLoaded(true)
        const file = ev.target.files[0]

        let fileType
        if (file !== undefined) {
            fileType = file.type
        } else {
            return
        }

        if (fileType === "image/png" || fileType === "image/jpeg" || fileType === "image/jpg") {
            const formdata: FormData = new FormData();
            formdata.append("file", file)

            axios.patch(`${process.env.REACT_APP_API_URL}/users/image/${userId}`, formdata)
                .then((res: any) => {
                    const { data } = res

                    dispatch(setNotification({
                        visible: true,
                        message: data.message,
                        type: "success",
                    }))
                    setLoaded(false)
                    setRefrech({})
                }).catch(error => {
                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! Réessayez plus tard...";
                    if (!type)
                        type = "error";

                    dispatch(setNotification({
                        visible: true,
                        message: message,
                        type
                    }))
                    setLoaded(false)
                })
        } else {
            dispatch(setNotification({
                visible: true,
                message: "Le fichier doit être au format (.jpeg, .jpg, .png)",
                type: "warning"
            }))
            setLoaded(false)
        }

    }

    const handleSubmit = (e: any): void => {
        e.preventDefault()
        const form = e.currentTarget
        const is_phone = isPhone(form.contact)
        const is_email = isEmail(form.email)
        const is_firstname = isFirstname(form.firstname)
        const is_lastname = isLastname(form.lastname)

        if (is_phone && is_email && is_firstname && is_lastname) {

            let formdata: any = {
                firstname: form.firstname.value,
                lastname: form.lastname.value,
                email: form.email.value,
                contact: form.contact.value
            }

            axios.patch(`${process.env.REACT_APP_API_URL}/users/${userId}`, formdata)
                .then((res: any) => {
                    const { data } = res

                    dispatch(setNotification({
                        visible: true,
                        message: data.message,
                        type: "success",
                    }))
                    setRefrech({})
                    setIsEdit(false)

                }).catch(error => {
                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! Réessayez plus tard...";
                    if (!type)
                        type = "error";

                    dispatch(setNotification({
                        visible: true,
                        message: message,
                        type
                    }))
                })
        }

    }
    const isPhone = (element: HTMLInputElement): boolean => {
        const error = document.getElementById('phone_error') as HTMLElement

        let regex: RegExp = /^6\d{8}$/
        let phone = element.value

        if (!regex.test(phone)) {
            element.style.borderColor = "var(--danger)"
            error.innerHTML = "Numero de téléphone invalide !"
            return false
        } else {
            error.innerHTML = ""
            element.style.borderColor = "var(--border)"
        }
        return true
    }

    const isEmail = (element: HTMLInputElement): boolean => {
        const error = document.getElementById('email_error') as HTMLElement

        let regex: RegExp = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
        const email = element.value

        if (!regex.test(email)) {
            element.style.borderColor = "var(--danger)"
            error.innerHTML = "Email invalide !"
            return false
        } else {
            error.innerHTML = ""
            element.style.borderColor = "var(--border)"
        }
        return true
    }
    const isFirstname = (element: HTMLInputElement): boolean => {
        let error: HTMLElement = document.getElementById("firstname_error") as HTMLElement
        const firstname = element.value
        if (firstname.length < 3) {
            element.style.borderColor = "var(--danger)"
            error.innerHTML = "Le nom est invalide !"
            return false
        } else {
            error.innerHTML = ""
            element.style.borderColor = "var(--border)"
        }
        return true
    }
    const isLastname = (element: HTMLInputElement): boolean => {
        let error: HTMLElement = document.getElementById("lastname_error") as HTMLElement
        const lastname = element.value
        if (lastname.length < 3) {
            element.style.borderColor = "var(--danger)"
            error.innerHTML = "Prénom invalide !"
            return false
        } else {
            error.innerHTML = ""
            element.style.borderColor = "var(--border)"
        }
        return true
    }

    return (
        <div className="user_card animated animatedFadeInUp fadeInUp">

            {loaded ? <a href='#lightbox' className="image">
                <Loader isLoading={loaded} width="100%" height="100%" size={.9} />
            </a> : (_id) &&
            <a
                href='#lightbox'
                className="image"
                style={{
                    backgroundImage: `url("${process.env.REACT_APP_API_URL}/uploads/profile/${image || "default.png"}?t=${isEdit ? "" : new Date().getTime()}")`
                }}
            >
                <label htmlFor="choiseImage">
                    <i className='fa fa-edit'></i>
                    <input onChange={uploadImage} type="file" id='choiseImage' hidden accept='image/jpg, image/png, image/jpeg' />
                </label>
            </a>
            }
            {(!_id) && <div className='not-internet'>
                <span>Problème de connexion ! <br /> <i className='fa fa-warning'></i></span>
                <br />
                <a className='default_link' href='/profile'> Actualisé <i className='fa fa-refresh'></i></a>
            </div>}
            {isEdit ? <form onSubmit={handleSubmit} className="">
                <input
                    type="text"
                    name='firstname'
                    placeholder='nom'
                    defaultValue={firstname}
                />
                <span id='firstname_error'></span>
                <input
                    type="text"
                    name='lastname'
                    placeholder='prénom'
                    defaultValue={lastname}
                />
                <span id='lastname_error'></span>
                <input
                    type="text"
                    name='email'
                    placeholder='email'
                    defaultValue={email}
                />
                <span id='email_error'></span>
                <input
                    type="number"
                    name='contact'
                    placeholder='contact'
                    defaultValue={contact}
                    style={{ paddingRight: 10 }}
                />
                <span id='phone_error'></span>
                {/* <input
                    type="text"
                    name='speciality'
                    placeholder='speciality'
                /> */}
                <div className="btn">
                    <button type='button' className="cancel" onClick={() => setIsEdit(false)}>
                        <i className='fa fa-edit'></i> Annuler
                    </button>
                    <button type='submit' className="submit">
                        <i className='fa fa-edit'></i> Enregister
                    </button>
                </div>
            </form> : (_id) &&
            <div className="text-content">
                <h3>
                    <a href='#lightbox'>{(firstname || "") + " " + (lastname || "")}</a>
                </h3>

                <div className="description">
                    <p style={{ opacity: .5 }}><i className="fa fa-envelope-o"></i> {email} </p>
                    <p><i className="fa fa-folder-o"></i> {speciality} </p>
                    <p style={{ cursor: 'pointer' }}><i className="fa fa-phone"></i>{contact ? "+237" : ""} {contact || "Ajouter un contact"} </p>
                    <button type='button' className="update" onClick={() => setIsEdit(true)}>
                        <i className='fa fa-edit'></i> Modifier le profil
                    </button>
                </div>
            </div>
            }
        </div>
    )
}

export default UserCard
