import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import Portal from './Portal'
import { setNotification } from '../feature/notification.slice'

const MatriculeChecker = (props: any) => {
  const { form, setForm } = props
  const [isMatricule, setIsMatricule] = useState<boolean>(false)

  const dispatch = useDispatch()

  const matricule = [
    "GLO3244",
    "GLO6999",
    "GLO3225",
    "GLO3493",
    "GLO3444",
  ]

  const handleMatricule = (e: any) => {
    e.preventDefault()
    const matriculeEl: HTMLInputElement = document.getElementById('matricule') as HTMLInputElement
    const span: HTMLInputElement = document.getElementById('matricule_error') as HTMLInputElement
    const matriculeBox: HTMLInputElement = document.getElementById('matriculeBox') as HTMLInputElement

    const value: string = matriculeEl.value.toUpperCase().trim()
    let test: boolean = matricule.includes(value)
    if (test) {
      setIsMatricule(true)
      matriculeEl.style.border = "1px solid var(--border)";
      matriculeBox.style.border = "1px solid var(--border)";
      span.textContent = ""
      dispatch(setNotification({
        type: "success",
        message: "Matricule valide. Vous etes autoriser a créer un compte !",
        visible: true
      }))
    } else {
      matriculeEl.style.border = "1px solid var(--danger)";
      matriculeBox.style.border = "1px solid var(--danger)";
      span.textContent = "matricule erroner !"

    }

  }

  return (
    <>
      {
        (form === "sign-up" && !isMatricule) &&
        <Portal>
          <div className="matricule_checker">
            <form
              onSubmit={handleMatricule}
              id='matriculeBox'
              className="matricule animated animatedFadeInUp fadeInUp"
            >
              <i
                className='fa fa-close'
                title='annuler'
                onClick={() => setForm("sign-in")}
              ></i>
              <h3>Entre votre matricule pour continuer</h3>
              <label htmlFor="matricule">
                Matricule
                <input
                  // autoFocus
                  type="text"
                  name='matricule'
                  placeholder='matricule etudiant'
                  id='matricule'
                />
                <span id='matricule_error'></span>
              </label>
              <button
                type='submit'
              >valider</button>
              <br />
              <span
                title='connectez vous !'
                onClick={() => setForm("sign-in")}
                className='default_link'
              >j'ai déjà un compte</span>
            </form>
          </div>
        </Portal>
      }
    </>)
}


export default MatriculeChecker

