import React, { useEffect, useState } from 'react'
import MatriculeChecker from '../components/MatriculeChecker'
import SignInForm from '../components/SignInForm'
import SignUpForm from '../components/SignUpForm'
import TopBar from '../components/TopBar'
import SideMenu from '../components/SideMenu'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { setSideMenuToggle } from '../feature/global.slice'
import Swipe from '../components/Swipe'

const LogIn = () => {
  const sessionToken = useSelector((store: any) => store.session.token)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  useEffect(() => {
    if (sessionToken) {
      navigate('/profile')
    }
  }, [sessionToken, navigate, dispatch])

  const mobile = window.matchMedia("(max-width: 950px)").matches
  const { sideMenuToggle } = useSelector((store: any) => store.global)
  const [form, setForm] = useState("sign-in")
  const [email, setEmail] = useState("")

  let title: string = ""
  switch (form) {
    case "sign-in": title = "se Connecter"
      break;
    default: title = "s'enregistrer";
      break;
  }

  const onSwipeRigth = (): void => {
    dispatch(setSideMenuToggle({ sideMenuToggle: false }))
  }

  return (
    <Swipe onSwipeRigth={onSwipeRigth}>
      <div className='login_page'>
        <nav>
          <SideMenu />
        </nav>
        <section id='LogIn_page' className={(mobile) ? (sideMenuToggle) ? '' : 'menu_open' : ''}>
          <TopBar currentPage={title} />
          <nav className='nav_login'>
            <ul>
              <li>
                <span
                  onClick={() => setForm("sign-in")}
                  className={form === "sign-in" ? 'active' : ''}
                >Se Connecter</span>
              </li>
              <li>
                <span
                  onClick={() => setForm("sign-up")}
                  className={form === "sign-up" ? 'active' : ''}
                >Créer un compte</span>
              </li>

            </ul>
          </nav>
          <main>
            {
              form === "sign-in" &&
              <SignInForm
                setForm={setForm}
                emailLog={email}
              />}
            {
              form === "sign-up" &&
              <SignUpForm
                setForm={setForm}
                setEmailLog={setEmail}
              />
            }
            <MatriculeChecker form={form} setForm={setForm} />
          </main>
        </section>
        <aside>
          <main>
            <div className="image_box animated animatedFadeInUp fadeInUp">
              {form === "sign-in" ?
                <img
                  src={`${process.env.PUBLIC_URL}images/kids-studying-from-home-rafiki-2712.png`}
                  alt="sign-in"
                /> : <img
                  src={`${process.env.PUBLIC_URL}images/architect-rafiki-4325.png`}
                  alt="sign-up"
                />
              }

            </div>
          </main>
        </aside>
      </div>
    </Swipe>
  )
}


export default LogIn

