import axios from "axios"
import React, { useEffect, useState } from "react"
import UserCard from "../components/UserCard"
import TopBar from "../components/TopBar";
import { useSelector } from "react-redux";
import { UserDatas } from "../_interface";
import SkeletonUserCard from "../components/SkeletonUserCard";
import UserBookCard from "../components/UserBookCard";


const Profile = () => {
  const [user, setUser] = useState<UserDatas>({
    _id: "",
    fullName: "",
    firstname: "",
    lastname: "",
    email: "",
    speciality: "",
    contact: "",
    level: "",
    image: undefined,
  })
  const [refrech, setRefrech] = useState<boolean>(false)
  const [isLoading, setIsLoading] = useState<boolean>(true)
  const { userId } = useSelector((store: any) => store.session)


  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/users/${userId}`)
      .then((res: any) => {
        const { data } = res.data
        setUser(data)
        setIsLoading(false)
      }).catch((error: any) => {
        console.log(error);
        setIsLoading(false)
      })
  }, [userId, refrech])

  return (
    <div className="profile_page">
      <TopBar currentPage="profil utilisateur" />
      {isLoading ? <SkeletonUserCard /> :
        <UserCard user={user} setRefrech={setRefrech} />}
      <UserBookCard
        books={user.books}
        isLoading={isLoading}
      />
    </div>
  )
}

export default Profile
