import React, { useRef, useEffect } from 'react'
import Hammer from "hammerjs";

const Swipe = (props: any) => {
    const { onSwipeLeft, onSwipeRigth, children } = props;

    const swipeRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const hammer = new Hammer(swipeRef.current!);
        hammer.on("swipeleft", onSwipeLeft);
        hammer.on("swiperight", onSwipeRigth);

        return () => {
            hammer.off("swipeleft", onSwipeLeft);
            hammer.off("swiperight", onSwipeRigth);
        };
    }, [onSwipeLeft, onSwipeRigth]);

    return <div ref={swipeRef}>{children}</div>;
};

export default Swipe;