const { createSlice } = require("@reduxjs/toolkit");
let mobile = window.matchMedia("(max-width: 950px)").matches;
export const globalSlice: any = createSlice({
  name: "global",
  initialState: {
    sideMenuToggle: mobile,
    asideMenuToggle: mobile,
  },

  reducers: {
    setSideMenuToggle: (state: any, action: any) => {
      const { payload } = action;
      state.sideMenuToggle = payload.sideMenuToggle;
      // state.asideMenuToggle = true;
    },
    setAsideMenuToggle: (state: any, action: any) => {
      const { payload } = action;
      state.asideMenuToggle = payload.asideMenuToggle;
      if (mobile) {
        state.sideMenuToggle = true;
      }
    },
  },
});

export const { setSideMenuToggle, setAsideMenuToggle, setIsAddForm } =
  globalSlice.actions;
