import { sessionSlice } from "../feature/session.slice";
import { notificationSlice } from "../feature/notification.slice";
import { periodeSlice } from "../feature/periode.slice";
import { globalSlice } from "../feature/global.slice";

const { configureStore } = require("@reduxjs/toolkit");

export const store: any = configureStore({
  reducer: {
    notification: notificationSlice.reducer,
    session: sessionSlice.reducer,
    periode: periodeSlice.reducer,
    global: globalSlice.reducer,
  },
});
