import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import axios from 'axios'
import TopBar from '../components/TopBar';
import { BookDatas, UserDatas } from '../_interface';
import GetAccessBook from '../components/GetAccessBook';
import Cookies from 'js-cookie';

function BookDetail(props: any) {
    const { id } = useParams()
    const [code, setCode] = useState<boolean>(false)
    const [viewPdf, setViewPdf] = useState<boolean>(false)



    const [book, setBook] = useState<BookDatas>({
        _id: "",
        _idUser: "",
        thumbnail: "",
        pdfName: "",
        docxName: "",
        theme: "",
        option: "",
        level: 0,
        level_value: "",
        year: "",
        description: undefined
    })
    const [user, setUser] = useState<UserDatas>({
        _id: "",
        fullName: "",
        firstname: "",
        lastname: "",
        email: "",
        speciality: "",
        level: "",
        image: "",
        books: [],
    })

    const userId = Cookies.get("userId")
    const myBook: boolean = userId === book._idUser

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/book/${id}`)
            .then((res: any) => {
                const { data } = res.data
                setBook(data)
            }).catch((error: any) => {
                console.log(error);
            })
    }, [id])
    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/users/${book._idUser}`)
            .then((res: any) => {
                const { data } = res.data
                setUser(data)
            }).catch((error: any) => {
                console.log(error);
            })
    }, [book._idUser])


    return (
        <>
            <TopBar currentPage="details" />
            <GetAccessBook visible={code} setVisible={setCode} contact={user.contact} />
            {!viewPdf ? <div id="book_details">
                <div
                    className="doc_preview animated animatedFadeInUp fadeInUp"
                    style={{
                        background: `linear-gradient(
                        to top, 
                        rgb(0 0 0 / .9), rgb(0 0 0 / 0)
                      ), url(${process.env.REACT_APP_API_URL}/uploads/${book.thumbnail})`
                    }}
                >
                    <div className="buttons">
                        <button
                            className='btn_show'
                            onClick={() => setViewPdf(true)}
                        > <i className='fa fa-eye'></i></button>

                        {(myBook) ? <a style={{ textDecoration: "none" }} href={`${process.env.REACT_APP_API_URL}/uploads/${book.pdfName}`}>
                            <button
                                className='btn_download'
                            > <i className='fa fa-download'></i></button>
                        </a> :
                            <button
                                className='btn_download'
                                onClick={() => setCode(true)}
                            > <i className='fa fa-download'></i></button>}
                        {/* </a> */}
                    </div>
                </div>
                <div
                    className="description animated animatedFadeInUp fadeInUp"
                    style={{ animationDelay: "200ms" }}
                >
                    <div className="user_detail">
                        <a
                            href='#lightbox'
                            className="image"
                            style={{
                                backgroundImage: `url("${process.env.REACT_APP_API_URL}/uploads/profile/${user.image || "default.png"}")`
                            }}
                        >&nbsp;</a>
                        <div className="text-content">
                            <h3 className="">
                                <a href='#lightbox'>{user.fullName}</a>
                            </h3>
                            <p><i className="fa fa-folder-o"></i> {user.speciality} </p>

                            <a
                                target='_blank'
                                rel="noopener noreferrer"
                                title='connectez le propriétaire !'
                                href={`https://wa.me/237${user.contact}?text=internship-report:%20je%20souhaite%20utiliser%20votre%20rapport%20de%20stage`}>
                                <button className="option">
                                    <i className='fa fa-phone'></i>
                                    Contacter
                                </button>
                            </a>
                        </div>
                    </div>

                    <div className="book">
                        <h2 className="theme">{book.theme}</h2>
                        <div className="info">
                            <span style={{ opacity: .7 }}><i className="fa fa-bookmark-o"></i> {book.option} </span>
                            <span><i className="fa fa-folder-o"></i> {book.level_value} </span>
                            <span style={{ opacity: .7 }}><i className="fa fa-calendar"></i> {book.year}</span>
                        </div>

                        <div className="text_box">
                            {book?.description}
                        </div>
                    </div>
                </div>
            </div> : <canvas id="pdf-canvas"></canvas>}


        </>
    );
}

export default BookDetail;