import Cookies from "js-cookie";
import { UserSession } from "../_type";

const { createSlice } = require("@reduxjs/toolkit");

const token = Cookies.get("token");

export const sessionSlice: any = createSlice({
  name: "session",
  initialState: {
    userId: "",
    token: "",
    email: "",
    connected: !!token,
    isAdmin: 0,
    contact: "",
    firstname: "",
    lastname: "",
    speciality: "",
  },

  reducers: {
    connectUser: (state: UserSession, action: any) => {
      // {type: "session/connectUser" payload: 1}
      const { payload } = action;
      state.userId = payload.userId;
      state.email = payload.email;
      state.connected = payload.connected;
      state.isAdmin = payload.isAdmin;
      state.token = payload.token;
      state.contact = payload.contact;
      state.firstname = payload.firstname;
      state.lastname = payload.lastname;
      state.speciality = payload.speciality;
    },
    disconnectUser: (state: UserSession, _action: any) => {
      state.userId = "";
      state.email = "";
      state.connected = false;
      state.isAdmin = 0;
      state.token = "";
    },
  },
});

export const { connectUser, disconnectUser } = sessionSlice.actions;
