import { BookDatas } from "../_interface"
import Card from "./Card"
import SkeletonCard from "./SkeletonCard"
import { useNavigate } from "react-router-dom"

const UserBookCard = (props: any) => {
    const navigate = useNavigate()
    const { books, isLoading } = props
    return <div className="user_book" style={{ position: 'relative' }}>
        <div className="header">
            <h3>Mes documents</h3>
            <button onClick={() => navigate('/add_book')}>
                <i className="fa fa-plus"></i> Ajouter un document
            </button>
        </div>
        {isLoading ?
            [1, 2].map((_, key) => <SkeletonCard key={key} />) :
            books?.map((book: BookDatas, key: any) =>
                <Card
                    book={book}
                    key={key}
                />)
        }
    </div>
}
export default UserBookCard