import React, { useEffect, useState } from 'react'
import TopBar from '../components/TopBar'
import { useDispatch, useSelector } from 'react-redux'
import { Periode } from '../_type'
import { useNavigate } from 'react-router-dom'
import { setPeriode } from '../feature/periode.slice'
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import axiosURL from '../axiosConfig'
import { AxiosResponse, AxiosError } from "axios";



const Home: React.FC = () => {
  const [specialities, setSpecialities] = useState<[]>([])
  const [isLoading, setLoading] = useState<boolean>(false)
  const navigate = useNavigate()

  const dispatch = useDispatch()

  const { option }: Periode = useSelector((store: any) => store.periode)

  const handleSpeciality = (e: any) => {
    const option: string = e.currentTarget.dataset.option

    dispatch(setPeriode({ option }))

    navigate('/bookstore')
  }

  useEffect(() => {
    setLoading(true)
    dispatch(setPeriode({ option: "" }))

    axiosURL.get(`${process.env.REACT_APP_API_URL}/option`)
      .then((res: AxiosResponse) => {
        const { data } = res.data
        setSpecialities(data)
        setLoading(false)
      }).catch((error: AxiosError) => {
        console.log(error);
        setLoading(false)
      })

  }, [option, dispatch])

  return (
    <div className="home_page">
      <TopBar currentPage="accueil" />
      <h2>Choisissez une <span className='text-accent'>Spécialité</span>! </h2>
      <div className="cards">
        {(!isLoading) ?
          specialities.map((speciality: any, key: number) =>
            <div
              key={key}
              style={{
                background: `linear-gradient(
                to top, 
                rgb(0 0 0 / .9), rgb(0 0 0 / 0)
              ), url("${process.env.REACT_APP_API_URL}/uploads/options/${speciality.image}")`
                // ), url(${process.env.PUBLIC_URL}images/option-0${key + 1}.jpg)`
              }}
              className='card_option'
              onClick={handleSpeciality}
              data-option={speciality.value}
            >
              <div className="text_content">
                <p className='short'>{speciality.short}</p>
                <p>{speciality.value}</p>
              </div>
            </div>
          ) : [1, 2, 3, 4, 5].map((_, key) => <SkeletonTheme
            key={key}
            baseColor="var(--skeleton-base-color)"
            highlightColor="var(--skeleton-highlight-color)"
          >
            <Skeleton
              height={200}
              width={"100%"}
            />
          </SkeletonTheme>)}
      </div>
    </div>

  )
}
export default Home