import { useState } from 'react'
import Cookies from "js-cookie";
import BoxConfirm from "./BoxConfirm";
import Portal from "./Portal";
import { useDispatch } from 'react-redux';
import { disconnectUser } from '../feature/session.slice';
import { setNotification } from '../feature/notification.slice';
import { setAsideMenuToggle, setSideMenuToggle } from '../feature/global.slice';
import { useNavigate } from 'react-router-dom';



const LogOut = () => {
    const mobile = window.matchMedia("(max-width: 950px)").matches
    const [confirm, setConfirm] = useState(false)
    const [showPopup, setShowPopup] = useState(false)
    const navigate = useNavigate()
    const dispatch = useDispatch()

    const handleLogOut = () => {
        setShowPopup(true)
    }
    if (confirm) {
        Cookies.remove("token")
        Cookies.remove("userId")
        dispatch(disconnectUser({}))
        dispatch(setNotification({
            type: "info",
            message: "vous étes actuelement déconnecter",
            visible: true
        }))
        navigate('/sign')
        if (mobile) {
            dispatch(setAsideMenuToggle({ asideMenuToggle: true }))
            dispatch(setSideMenuToggle({ sideMenuToggle: true }))
        }
    }

    return (
        <>
            <Portal>
                <BoxConfirm
                    visibility={showPopup}
                    setVisibility={setShowPopup}
                    message='voulez vous vraiment vous déconnecter ?'
                    setConfirm={setConfirm}
                />
            </Portal>
            <div className='log_out_btn' onClick={handleLogOut}>
                <i className='fa fa-sign-out'></i> <span>Se Déconnecter</span>
            </div>
        </>
    )
}

export default LogOut
