import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import LogOut from './LogOut'
import { useDispatch, useSelector } from 'react-redux'
import { setSideMenuToggle } from '../feature/global.slice'

const SideMenu = () => {
    const [matches] = useState<boolean>(window.matchMedia("(max-width: 950px)").matches)
    const dispatch = useDispatch()

    const { connected } = useSelector((store: any) => store.session)
    const { sideMenuToggle } = useSelector((store: any) => store.global)

    const autoToggle = (): (false | void) => matches && dispatch(setSideMenuToggle({ sideMenuToggle: !sideMenuToggle }))

    // define a function that sets min-height of my-element to window.innerHeight:
    const setHeight = () => {
        const menu = document.getElementById("side_menu")
        if (menu) menu.style.height = window.innerHeight + "px"
    };

    // define mobile screen size:
    let deviceWidth = window.matchMedia("(max-width: 1024px)");

    if (deviceWidth.matches) {
        // set an event listener that detects when innerHeight changes:
        window.addEventListener("resize", setHeight);

        // call the function once to set initial height:
        setHeight();
    }

    return (
        <>
            <nav
                id="side_menu"
                className={sideMenuToggle ? "close" : ''}
                onTouchMove={() => dispatch(setSideMenuToggle({ sideMenuToggle: true }))}
            >
                <div
                    className="mobile_top_bar"
                    onClick={() => dispatch(setSideMenuToggle({ sideMenuToggle: !sideMenuToggle }))}
                >
                    <i className={sideMenuToggle ? "fa fa-bars" : "fa fa-close"}></i>
                </div>
                <div
                    className="overlay"
                    onClick={() => dispatch(setSideMenuToggle({ sideMenuToggle: true }))}
                    onTouchStart={() => dispatch(setSideMenuToggle({ sideMenuToggle: true }))}
                ></div>
                <nav className='nav_list' role="navigation">
                    <ul>

                        <div onClick={autoToggle}>
                            <NavLink
                                to="/add_book"
                                className={({ isActive }) => (isActive ? 'add_btn active' : 'add_btn')}
                            >
                                <i className='fa fa-plus'></i> <span>ajouter</span>
                            </NavLink>
                        </div>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-home'></i> Accueil</NavLink>
                        </li>
                        {/* <li onClick={autoToggle}>
                            <NavLink
                                to="/bookstore"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-home'></i> Librairie</NavLink>
                        </li> */}
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/profile"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-user'></i> Mon Profil</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/settings"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-cog'></i> Paramètres</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/contact"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-address-book'></i> Contacter</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/about"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-info'></i> A propos</NavLink>
                        </li>
                        <li onClick={autoToggle} className={connected ? 'log-out-btn' : ""}>
                            {!connected && <NavLink
                                to="/sign"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-sign-in'></i> Se connecter</NavLink>}
                        </li>
                    </ul>
                    {connected && <LogOut />}

                </nav>
            </nav>
        </>
    )
}

export default SideMenu
