import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Level } from '../_type';

function LevelTag(props: any) {
    const { level, setLevel } = props
    const [levels, setLevels] = useState<Level[]>([
        { _id: "1", level: 2, value: "BTS" },
        { _id: "2", level: 3, value: "Licence" },
        { _id: "3", level: 5, value: "Master" }
    ])



    const handleRadio = (e: any): void => {
        setLevel(parseInt(e.target.value))
        const levelCheck: (Level | undefined) = levels.find(lev => lev.level === parseInt(e.target.value))
        if (levelCheck) setLevel(levelCheck)
    }

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/level`)
            .then(res => {
                const { data } = res.data;
                if (data.length > 0) {
                    setLevels(data)
                }
            }).catch(err => {
                console.log(err);
            })
    }, [])

    return (
        <div className="level_tags">
            <p>Cycle <sup>*</sup></p>
            {
                levels.map((el: Level, key: any) => <label key={key} >
                    {el.level === level.level ? <input
                        onChange={handleRadio}
                        type="radio"
                        name='tags'
                        value={el.level}
                        id={el.value}
                        className='level_radio'
                        checked
                        hidden
                    /> : <input
                        onChange={handleRadio}
                        type="radio"
                        name='tags'
                        value={el.level}
                        id={el.value}
                        className='level_radio'
                        hidden
                    />
                    } <label className="item" htmlFor={el.value}>{el.value}</label>
                </label>)
            }
            {/* <span className="help_message"></span> */}
        </div>
    );
}

export default LevelTag;