import React from "react";


const BoxConfirm = (props: any) => {
    const { type, message, visibility, setConfirm, setVisibility } = props
    let color: string = "",
        icon: string = "";

    const closeNotification: any = (e: any) => {
        const choise: string = e.target.dataset.choise;
        const toast: any = document.querySelector(".box_confirm") as HTMLElement
        const touchScreen: any = document.querySelector(".touch_screen") as HTMLElement

        if (choise === "confirm") setConfirm(true)

        toast.classList.add("hide")
        touchScreen.style.display = "none"

        const timer = setTimeout(() => {
            toast.classList.remove("hide")
            touchScreen.style.display = "block"
            setVisibility(false)
        }, 800);
        return () => clearTimeout(timer);
    }

    switch (type) {
        case "error":
            color = "var(--danger)"
            icon = "fa fa-bug"
            break;
        case "success":
            color = "var(--success)"
            icon = "fa fa-check"
            break;
        case "warning":
            color = "var(--warning)"
            icon = "fa fa-exclamation-triangle"
            break;

        default:
            color = "var(--primary)"
            icon = "fa fa-info"
            break;
    }
    return (
        <>
            {visibility && <div className="box_confirm">
                <div className="touch_screen" onClick={closeNotification}></div>
                <div className="toast" style={{ borderColor: color }}>
                    <div className="icon" style={{ backgroundColor: color }}>
                        <i className={icon}></i>
                    </div>
                    <div
                        className="close-icon"
                        onClick={closeNotification}
                        data-choise='cancel'
                    >
                        <i className="fa fa-close" data-choise='cancel'></i>
                    </div>
                    <div className="details">
                        <p>{message}</p>
                    </div>
                    <button
                        onClick={closeNotification}
                        data-choise='confirm'
                        style={{ borderColor: color }}
                        className="btn_comfir"
                        autoFocus
                    >valider</button>
                    <button
                        onClick={closeNotification}
                        data-choise='cancel'
                        className="btn_cancel"
                    >annuler</button>
                </div>
            </div>}
        </>
    )

}

export default BoxConfirm
