import React from 'react'
import { Outlet } from 'react-router-dom'
import AsideBar from '../components/AsideBar'
import SideMenu from '../components/SideMenu'
import { useDispatch, useSelector } from 'react-redux'
import Swipe from '../components/Swipe'
import { setAsideMenuToggle, setSideMenuToggle } from '../feature/global.slice'

export default function Layout() {
    const mobile = window.matchMedia("(max-width: 950px)").matches
    const { sideMenuToggle } = useSelector((store: any) => store.global)

    const dispatch = useDispatch()
    const onSwipeLeft = (): void => {
        dispatch(setAsideMenuToggle({ asideMenuToggle: false }))
    }
    const onSwipeRigth = (): void => {
        dispatch(setSideMenuToggle({ sideMenuToggle: false }))
    }
    return (
        <div className='layout'>
            <SideMenu />
            <main className={(mobile) ? (sideMenuToggle) ? '' : 'menu_open' : ''}>
                <Swipe onSwipeRigth={onSwipeRigth} onSwipeLeft={onSwipeLeft}>
                    <Outlet />
                </Swipe>
            </main>
            <AsideBar />
        </div>
    )
}
