import React, { useEffect, useState } from 'react'
import Card from '../components/Card'
import axios from 'axios'
import { useSelector } from 'react-redux'
import Pagination from '../components/Pagination'
import { Periode } from '../_type'
import { BookDatas } from '../_interface'
import FilterPeriode from '../components/FilterPeriode'
import SkeletonCard from '../components/SkeletonCard'
import TopBarIllustration from '../components/TopBarIllustration'
import TopBar from '../components/TopBar'


const Bookstore: React.FC = () => {
  const [books, setBooks] = useState<BookDatas[]>([])
  const [isLoading, setLoading] = useState<boolean>(false)
  const [totalPages, setTotalPages] = useState<number>(1)
  const [empty, setEmpty] = useState<boolean>(false)
  const mobile: boolean = window.matchMedia("(max-width: 950px)").matches

  const { year, option, level_value, search, page }: Periode = useSelector((store: any) => store.periode)

  useEffect(() => {
    setLoading(true)

    const fetchData = (page: number) => {
      axios.get(`${process.env.REACT_APP_API_URL}/book?year=${parseInt(year)}&level_value=${level_value}&option=${option}&search=${search}&page=${page}`)
        .then((res: any) => {
          const { data } = res.data
          setBooks(data)
          setTotalPages(res.data.totalPages)
          setLoading(false)
          if (data.length === 0) setEmpty(true)
          else setEmpty(false)

        }).catch((error: any) => {
          console.log(error);
          setLoading(false)
        })
    }
    fetchData(page)
  }, [search, year, option, level_value, page])

  const markBookText: Function = (): BookDatas[] => {
    if (search !== "" && search !== "tous") {
      const regex = new RegExp(search, 'gi');

      const highlightedBook: BookDatas[] = books.map((book) => {
        const highlightedTheme = book.theme.replace(regex, (match) => `<mark>${match}</mark>`);
        return { ...book, theme: highlightedTheme };
      });
      return highlightedBook
    } else return books
  }

  return (
    <div className="bookstore app_title">
      {!mobile ? <>
        <TopBar currentPage="Librairie" />
        <div
          style={{
            background: `linear-gradient(
                to top, 
                rgb(0 0 0 / .9), rgb(0 0 0 / 0)
              ), url(${process.env.PUBLIC_URL}images/library.jpg)`,
          }}
          className='card_option'
        >
          <div className="text_content">
            <p className='short'>Librairie</p>
            {option ? <p>Retrouvez tous les rapports de stage de la filière <span className='text-accent'>{option}</span></p>
              : <p>Retrouvez tous les rapports de stage ici</p>}
          </div>
        </div>
      </> :
        <TopBarIllustration
          option={option}
          title="Librairie"
          text="Retrouvez tous les rapports de stage ici"
          url={`url(${process.env.PUBLIC_URL}images/library.jpg)`}
        />

      }
      <div className="cards">
        {(!isLoading) ?
          markBookText().map((book: BookDatas, key: number) =>
            <Card
              key={key}
              count={key}
              book={book}
            />
          ) : [1, 2, 3, 4, 5].map((_, key) => <SkeletonCard key={key} />)}

        {empty && <div className='data_not_found'>
          <div style={{ textAlign: "center" }}>
            <FilterPeriode onHomePage={true} />
            <h2>Aucun résultat pour le moment...</h2>
            <img
              style={{
                display: "block",
                width: "40%",
                margin: "auto",
                marginTop: 20
              }}
              src={`${process.env.PUBLIC_URL}images/undraw_empty_re_opql.svg`} alt="empty" />
          </div>
        </div>}
      </div>
      {books.length !== 0 && <Pagination totalPages={totalPages} />}
    </div>

  )
}
export default Bookstore