import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { setPeriode } from '../feature/periode.slice';
import { useLocation, useNavigate } from 'react-router-dom';
import { setAsideMenuToggle } from '../feature/global.slice';

const SearchBar = () => {
    const [matches] = useState(window.matchMedia("(max-width: 950px)").matches)
    const [search, setSearch] = useState<string | undefined>()
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { pathname } = useLocation()

    const handelSearch: any = (ev: any) => {
        ev.preventDefault()
        dispatch(setPeriode({ search: search?.trim() }))
        setSearch("")
        if (pathname !== '/bookstore') {
            navigate('/bookstore')
        }
        if (matches) {
            dispatch(setAsideMenuToggle({ asideMenuToggle: true }))
        }
    }

    return (
        <>
            <form onSubmit={handelSearch} id="Search_bar" className="js-fullheight">
                <div className="search_group">
                    <input
                        type="text"
                        placeholder='Rechercher...'
                        onChange={(ev: any) => setSearch(ev.target.value)}
                        value={search}
                    />
                    <button id='btnSearch' type='submit'>
                        <i
                            style={{ color: "var(--primary)" }}
                            className='fa fa-search'
                        ></i>
                    </button>
                </div>
            </form>
        </>
    )
}

export default SearchBar
