import React from 'react'
import { useNavigate } from 'react-router-dom'

function TopBarIllustration(props: any) {
    const { title, text, option, url } = props
    const navigate = useNavigate();
    const currentPath = window.location.pathname

    const rootPath: boolean = currentPath === "/" || currentPath === "/home"

    const goBack = (): void => {
        if (currentPath !== "/") {
            navigate("/")
            // navigate(-1)
        }
    }

    return (
        <div
            className='top_bar_illustration'
            style={{
                background: `linear-gradient(
                to top, 
                rgb(0 0 0 / .9), rgb(0 0 0 / 0)
              ), ${url}`,
            }}
        >

            <div className="text_content">
                <p className='short'>
                    <i
                        className={rootPath ? 'fa fa-angle-left disable' : 'fa fa-angle-left'}
                        onClick={goBack}
                    ></i>
                    {title}
                </p>
                {option ? <p>Retrouvez tous les rapports de stage de la filière {option}</p>
                    : <p>{text}</p>}
            </div>
        </div>
    )
}

export default TopBarIllustration