import React, { useEffect, useState } from 'react'
import { Routes, Route } from "react-router-dom"
import About from './pages/About'
import PrivateRoutes from './PrivateRoutes'
import Contact from './pages/Contact'
import LogIn from './pages/LogIn'
import Settings from './pages/Settings'
import Profile from './pages/Profile'
import NotFound from './pages/NotFound'
import BookDetail from './pages/BookDetail'
import Layout from './pages/Layout'
import Cookies from 'js-cookie'
import { connectUser } from './feature/session.slice'
import axios from 'axios'
import { useDispatch } from 'react-redux'
import { darkTheme, lightTheme } from './_theme'
import StartComponent from './components/StartComponent'
import Home from './pages/Home'
import Bookstore from './pages/Bookstore'
import AddBook from './pages/AddBook'
import EditBook from './pages/EditBook'
import AdminLayout from './dashboard/AdminLayout'
import AdminHome from './dashboard/AdminHome'


const Routeur = () => {
    const dispatch = useDispatch()
    const userId: string = Cookies.get("userId") || "";
    const token: string = Cookies.get("token") || "";
    const [isFirstTime, setIsFirstTime] = useState(!(!!window.localStorage.getItem("isFirstTime")))

    useEffect(() => {
        if (userId) {
            axios.get(`${process.env.REACT_APP_API_URL}/users/${userId}`)
                .then((res: any) => {
                    const { data } = res.data
                    data.userId = userId
                    data.token = token
                    data.connected = true
                    data.isAdmin = 1
                    dispatch(connectUser(data))
                })
                .catch((error: any) => {
                    console.log(error);
                });
        }
    }, [dispatch, userId, token])

    useEffect(() => {
        if (localStorage.getItem("theme") === "light") {
            lightTheme()
        }
        else if (localStorage.getItem("theme") === "dark") {
            darkTheme()
        }
        else localStorage.setItem("theme", "light")
    }, [])

    // window.addEventListener("scroll", function () {
    //     window.scroll(0, window.pageYOffset)
    // }, false);

    return (
        <>
            {isFirstTime && <StartComponent setIsFirstTime={setIsFirstTime} />}
            <Routes>
                <Route element={<Layout />}>
                    <Route path="/" element={<Home />} />
                    <Route path="/bookstore" element={<Bookstore />} />
                    <Route element={<PrivateRoutes />}>
                        <Route path="/profile" element={<Profile />} />
                        <Route path="/add_book" element={<AddBook />} />
                        <Route path="/edit_book/:id" element={<EditBook />} />
                    </Route>
                    <Route path="/detail/:id" element={<BookDetail />} />
                    <Route path="/settings" element={<Settings />} />
                </Route>
                <Route element={<AdminLayout />}>
                    <Route path="/admin" element={<AdminHome />} />
                </Route>
                <Route path="/sign" element={<LogIn />} />
                <Route path="/about" element={<About />} />
                <Route path="/contact" element={<Contact />} />

                <Route path="*" element={<NotFound />} />
            </Routes>
        </>
    )
}

export default Routeur
