import React from 'react';
import Loader from './Loader';

function LoadBox(props: any) {
    const { isLoading, size } = props
    return (
        <>
            {
                isLoading && <div
                    style={{
                        position: "absolute",
                        height: "100%",
                        width: "100%",
                        top: 0,
                        left: 0,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        backgroundColor: "var(--background)",
                        opacity: 0.5,
                        zIndex: 4,

                    }}
                    className="load_box"
                >
                    <Loader width={size || 60} isLoading={isLoading} />
                </div>
            }
        </>
    );
}

export default LoadBox;