import React, { useState, useEffect } from 'react'
import axios from "axios"
import InputPassword from './InputPassword'
import { useDispatch } from 'react-redux'
import { setNotification } from '../feature/notification.slice'
import { Level, SpecialityModel, User } from '../_type'
import LevelTag from './LevelTag'
import LoadBox from './LoadBox'
import SelectCustom from './SelectCustom'


const SignUpForm = (props: any) => {
    const { setEmailLog } = props
    const { setForm } = props
    const [firstname, setFirstname] = useState<string>("")
    const [lastname, setLastname] = useState<string>("")
    const [email, setEmail] = useState<string>("")
    const [phone, setPhone] = useState<string>("")
    const [speciality, setSpeciality] = useState<string>("")
    const [loaded, setLoaded] = useState<boolean>(false)
    const [password, setPassword] = useState<string>("")
    const [level, setLevel] = useState<Level>({ _id: "1", level: 2, value: "BTS" }) //implicite type = Number

    const dispatch = useDispatch()

    const userData: User = {
        firstname: firstname.trim(),
        lastname: lastname.trim(),
        email: email.trim(),
        contact: phone,
        speciality: speciality,
        level: level.level,
        level_value: level.value,
        password: password,
    }

    const [option, setOption] = useState<SpecialityModel[]>([])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/option`).then((res: any) => {
            const { data } = res.data
            setOption(data)
        }).catch((error: any) => {
            console.log(error);
        })
    }, [])

    const handleFirstname: any = (e: any) => {
        setFirstname(e.target.value);
    }
    const handleLastname: any = (e: any) => {
        setLastname(e.target.value);
    }
    const handleEmail: any = (e: any) => {
        setEmail(e.target.value);
    }
    const handlePhone: any = (e: any) => {
        setPhone(e.target.value);
    }
    const handlePassword: any = (e: any) => {
        setPassword(e.target.value);
    }


    const handleSignUp: any = (e: any) => {
        e.preventDefault()

        const Lastname: boolean = isLastname();
        const Phone: boolean = isPhone();
        const Firstname: boolean = isFirstname();
        const Email: boolean = isEmail();
        const Password: boolean = isPassword();
        const Speciality: boolean = isSpeciality();
        const isLevel: boolean = !!level.level && !!level.value

        if (Lastname && Phone && Firstname && Email && Password && Speciality && isLevel && !loaded) {
            setLoaded(true)

            axios.post(`${process.env.REACT_APP_API_URL}/users/sign-up`, userData)
                .then(res => {
                    setLoaded(false)
                    dispatch(setNotification({
                        type: res.data.type,
                        message: res.data.message,
                        visible: true
                    }))
                    setForm("sign-in")
                    setEmailLog(email)
                    setEmail("")
                    setFirstname("")
                    setLastname("")
                    setPassword("")
                })
                .catch(error => {
                    setLoaded(false)
                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! reéssayer plus tard...";
                    if (!type)
                        type = "error";

                    dispatch(setNotification({
                        type: type,
                        message: message,
                        visible: true
                    }))
                })
        }
    }

    const isFirstname = (): boolean => {
        let element: HTMLElement = document.getElementById("firstname") as HTMLElement
        let error: HTMLElement = document.getElementById("firstname_error") as HTMLElement

        if (firstname.length < 3) {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return false
        } else {
            element.style.borderColor = "var(--border)"
            error.style.display = "none"
        }
        return true
    }
    const isLastname = (): boolean => {
        let element: HTMLElement = document.getElementById("lastname") as HTMLElement
        let error: HTMLElement = document.getElementById("lastname_error") as HTMLElement

        if (lastname.length < 3) {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return false
        } else {
            element.style.borderColor = "var(--border)"
            error.style.display = "none"
        }
        return true
    }
    const isEmail = (): boolean => {
        let element: HTMLElement = document.getElementById("email") as HTMLElement
        let error: HTMLElement = document.getElementById("email_error") as HTMLElement
        let regex: RegExp = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/

        if (!regex.test(email)) {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return false
        } else {
            error.style.display = "none"
            element.style.borderColor = "var(--border)"
        }
        return true
    }
    const isPhone = (): boolean => {
        let element: HTMLElement = document.getElementById("phone") as HTMLElement
        let error: HTMLElement = document.getElementById("phone_error") as HTMLElement
        let regex: RegExp = /^6\d{8}$/

        if (!regex.test(phone)) {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return false
        } else {
            error.style.display = "none"
            element.style.borderColor = "var(--border)"
        }
        return true
    }

    const isSpeciality = (): boolean => {
        let element: HTMLElement = document.getElementById("speciality") as HTMLElement
        let error: HTMLElement = document.getElementById("speciality_error") as HTMLElement
        let valid: boolean = false

        if (speciality) {
            element.style.borderColor = "var(--border)"
            error.style.display = "none"
            valid = true
            return valid
        } else {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return valid
        }
    }

    const isPassword = (): boolean => {
        let element: HTMLElement = document.getElementById("password") as HTMLElement
        let error: HTMLElement = document.getElementById("password_error") as HTMLElement
        if (password.length < 8) {
            element.style.borderColor = "var(--danger)"
            error.style.display = "block"
            return false
        } else {
            element.style.borderColor = "var(--border)"
            error.style.display = "none"
        }
        return true
    }

    return (
        <div className="login_form">
            <LoadBox isLoading={loaded} />
            <form className="animated animatedFadeInUp fadeInUp">
                <label htmlFor="">Nom Complet <sup>*</sup></label>
                <div className="user_name">
                    <input
                        type="text"
                        placeholder='Nom'
                        onChange={handleLastname}
                        value={lastname}
                        id="lastname"
                    />
                    <input
                        type="text"
                        placeholder='Prénon'
                        onChange={handleFirstname}
                        value={firstname}
                        id="firstname"
                    />
                    <p>
                        <span id='lastname_error'>Saisissez votre véritable nom svp !</span>
                    </p>
                    <p>
                        <span id='firstname_error'>Saisissez votre véritable prénom svp !</span>
                    </p>
                </div>

                <label htmlFor="">Email <sup>*</sup></label>
                <input
                    type="email"
                    placeholder='example@gmail.com'
                    onChange={handleEmail}
                    value={email}
                    id="email"
                />
                <span id='email_error'>Email invalide !</span>

                <label htmlFor="">Téléphone <sup>*</sup></label>
                <input
                    type="tel"
                    placeholder='699######'
                    onChange={handlePhone}
                    value={phone}
                    id="phone"
                />
                <span id='phone_error'>Entrez un numéro de téléphone valide !</span>

                <label htmlFor="">Spécialité <sup>*</sup> </label>
                <SelectCustom
                    id="speciality"
                    options={option}
                    setSpeciality={setSpeciality}
                />
                <span id='speciality_error'>Sellectionnez une spécialité dans la liste !</span>

                <LevelTag level={level} setLevel={setLevel} />

                <label htmlFor="">Mot De Passe <sup>*</sup></label>
                <InputPassword
                    password={password}
                    handlePassword={handlePassword}
                    placeholder="Minimum 8 caractéres"
                />
                {/* <label htmlFor="">Confirmer le Mot De Passe <sup>*</sup></label>
                <InputPassword
                    password={password}
                    handlePassword={handlePassword}
                /> */}
                <span id='password_error'>8 charactère minimum pour le mot de passe</span>
                <button
                    type="submit"
                    onClick={handleSignUp}
                    style={{ marginTop: 10 }}
                >
                    {!loaded ? "S'enregistre" : "chargement..."}
                </button>
            </form>
        </div>
    )
}

export default SignUpForm
