import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Level } from '../_type';
import DropTags from './DropTags';
import Portal from './Portal';
import ThemeSelector from './ThemeSelector';

function StartComponent(props: any) {
    const { setIsFirstTime } = props
    const [stape, setStape] = useState<number>(1)
    const [level, setLevel] = useState<Level[]>([
        { _id: "1", level: 2, value: "BTS" },
        { _id: "1", level: 3, value: "Licence" },
        { _id: "1", level: 5, value: "Master" }
    ])
    const [, setLoading] = useState(false)



    useEffect(() => {
        setLoading(true)
        axios.get(`${process.env.REACT_APP_API_URL}/level`).then((res: any) => {
            const { data } = res.data

            if (data.length !== 0) {
                setLevel(data)
            }
            setLoading(false)

        }).catch((error: any) => {
            setLoading(false)
            console.log(error);
        })
    }, [])

    const { level_value }: any = useSelector((store: any) => store.periode)


    const handleNavigate = (e: any) => {
        const action = e.currentTarget.dataset.action
        switch (action) {
            case "prev":
                if (stape !== 1) {
                    setStape(stape - 1)
                }
                break;
            case "next":
                if (stape === 2 && level_value) {
                    setStape(stape + 1)
                }
                if (stape === 1) {
                    setStape(stape + 1)
                }
                if (stape === 3) {
                    setStape(stape + 1)
                }
                if (stape === 4) {
                    window.localStorage.setItem("isFirstTime", "1")
                    setIsFirstTime(false)
                }
                break;
        }
    }


    return (
        <Portal>
            <div className="start_component">
                <div className="content_slide">
                    {stape === 1 &&
                        <div className='welcome'>
                            <div className="illustration">
                                <img height={350} src={`${process.env.PUBLIC_URL}/images/welcome-amico-1103.png`} alt="illustration" />
                            </div>
                        </div>}
                    {stape === 2 &&
                        <div className='cycle_stape'>
                            <p className='text'>choisissez votre cycle d'étude actuel ! </p>
                            <img src={`${process.env.PUBLIC_URL}/images/kids-studying-from-home-rafiki-2712.png`} alt="illustration" />
                            <DropTags
                                libele="Cilcle"
                                datas={level}
                                type="level_value"
                                open={true}
                                icon="fa fa-tags"
                                isLoading={false}
                            />
                        </div>
                    }
                    {stape === 3 &&
                        <div className='theme'>
                            <h3>definissez votre style</h3>
                            <ThemeSelector />
                        </div>
                    }
                    {stape === 4 &&
                        <div className='welcome end'>
                            <div className="illustration">
                                <img src={`${process.env.PUBLIC_URL}/images/notebook-rafiki-923.png`} alt="illustration" />
                            </div>
                            <p className='text'> partagez votre creativité avec les/ou futur ingénieurs du domaine !</p>
                        </div>
                    }

                    <div className="navigation">
                        <button
                            type='button'
                            onClick={(e) => handleNavigate(e)}
                            className='prev'
                            data-action='prev'
                        ><i className='fa fa-angle-left'></i> prev
                        </button>

                        <div className="stapes">
                            <div className={stape === 1 ? "stape active" : "stape"}></div>
                            <div className={stape === 2 ? "stape active" : "stape"}></div>
                            <div className={stape === 3 ? "stape active" : "stape"}></div>
                            <div className={stape === 4 ? "stape active" : "stape"}></div>
                        </div>

                        <button
                            type='button'
                            onClick={(e) => handleNavigate(e)}
                            className={stape === 2 && !level_value ? "next disable" : "next"}
                            title={stape === 2 && !level_value ? "selectionnez une option avant de continuer" : ""}
                            data-action='next'
                        >{stape === 4 ? "ternimer" : "suivant"} <i className='fa fa-angle-right'></i>
                        </button>
                    </div>
                </div>
            </div>
        </Portal>
    );
}

export default StartComponent;