import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux"
import { setNotification } from "../feature/notification.slice";

const ToastNotification = () => {
    const { type, message, visible } = useSelector((store: any) => store.notification)
    let color: string = "",
        icon: string = "",
        title: string = "";

    const dispatch = useDispatch()

    const closeNotification: any = () => {
        const toast: any = document.querySelector(".ToastNotification") as HTMLInputElement
        toast.classList.add("hide")

        const timer = setTimeout(() => {
            toast.classList.remove("hide")
            dispatch(setNotification({ visible: false }))
        }, 2000);
        return () => clearTimeout(timer);
    }

    useEffect(() => {
        if (visible) {
            const timer = setTimeout(() => {
                const toast: any = document.querySelector(".ToastNotification") as HTMLInputElement
                toast.classList.add("hide")

                const timer2 = setTimeout(() => {
                    toast.classList.remove("hide")
                    dispatch(setNotification({ visible: false }))
                }, 2000);
                return () => clearTimeout(timer2);

            }, 5000);
            return () => clearTimeout(timer);
        }
    }, [visible, dispatch])

    switch (type) {
        case "error":
            color = "var(--danger)"
            icon = "fa fa-bug"
            title = "Alerte"
            break;
        case "success":
            color = "var(--success)"
            icon = "fa fa-check"
            title = "Succés"
            break;
        case "warning":
            color = "var(--warning)"
            icon = "fa fa-exclamation-triangle"
            title = "Avertissement"
            break;

        default:
            color = "var(--primary)"
            icon = "fa fa-info"
            title = "Information"
            break;
    }
    return (
        <>
            {visible && <div onTouchMove={closeNotification} className="ToastNotification">
                <div className="toast" style={{ borderColor: color }}>
                    <div className="icon" style={{ backgroundColor: color }}>
                        <i className={icon}></i>
                    </div>
                    <div className="details">
                        <span>{title}</span>
                        <p>{message}</p>
                    </div>
                    <div className="close-icon" onClick={closeNotification}>
                        <i className="fa fa-close"></i>
                    </div>
                </div>
            </div>}
        </>
    )

}

export default ToastNotification
