import axios from 'axios'
import React, { useState, useRef, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { setNotification } from '../feature/notification.slice'
import { Level } from '../_type'
import LevelTag from '../components/LevelTag'
import { useNavigate, useParams } from 'react-router-dom'
import { BookDatas } from '../_interface'
import LoadBox from '../components/LoadBox'
import TopBarIllustration from '../components/TopBarIllustration'
import TopBar from '../components/TopBar'

const EditBook = () => {
    const { id } = useParams()
    const navigate = useNavigate()
    const mobile: boolean = window.matchMedia("(max-width: 950px)").matches

    const [book, setBook] = useState<BookDatas>({
        _id: "",
        _idUser: "",
        thumbnail: "",
        pdfName: "",
        docxName: "",
        theme: "",
        option: "",
        level: 0,
        level_value: "",
        year: "",
        description: undefined
    })
    const [isChecked, setIsChecked] = useState<boolean>()
    const [pdf, setPdf] = useState<File | undefined>()
    const [docx, setDocx] = useState<File | undefined>()
    const refPdf = useRef<any>(null)
    const refDocx = useRef<any>(null)
    const [theme, setTheme] = useState<string>("")
    const [description, setDescription] = useState<string | undefined>("")
    const [themeMessage, setThemeMessage] = useState<string>("")
    const [loaded, setLoaded] = useState<boolean>(false)
    const [level, setLevel] = useState<Level>({ _id: "", level: 0, value: "" })

    const dispatch = useDispatch()

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/book/${id}`)
            .then((res: any) => {
                const { data } = res.data
                setBook(data)
                setLevel({ _id: "", level: data.level, value: data.level_value })
                setTheme(data.theme)
                setDescription(data.description)
            }).catch((error: any) => {
                console.log(error);
            })
    }, [id])


    const handlePdfFile = (e: any): void => {
        const file = e.target.files[0]

        if (!file) return
        if (file.type === "application/pdf") {
            setPdf(file)
        } else {
            setPdf(undefined)
        }
    }

    const handleDocxFile = (e: any): void => {
        const file = e.target.files[0]
        if (!file) return
        const isDocx: boolean = file.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
        const isDoc: boolean = file.type === "application/msword";
        if (isDoc || isDocx) {
            setDocx(file)
        } else {
            setDocx(undefined)
        }
    }

    const handleSubmitBook = (e: any): void => {
        e.preventDefault()
        const isLevel: boolean = !!level.level && !!level.value
        const isFile = isChecked ? (!!pdf && !!docx) : true

        if (isLevel && theme.length > 15 && isFile) {
            setThemeMessage("");
            setLoaded(true)

            const formdata: FormData = new FormData(e.currentTarget);

            const isFile: Function = (): string => {
                if (isChecked) return "true"
                else return "false"
            }
            if (pdf === undefined || docx === undefined) {
                formdata.delete('pdf')
                formdata.delete('docx')
            }
            formdata.append('level_value', level.value)
            formdata.append('_idUser', book._idUser)
            formdata.append('year', book.year)
            formdata.append('level', level.level.toString())
            formdata.append('isFile', isFile())

            axios.patch(`${process.env.REACT_APP_API_URL}/book/${book._id}`, formdata)
                .then((res: any) => {
                    const { data } = res

                    setLoaded(false)
                    if (data.type === "success") {
                        dispatch(setNotification({
                            visible: true,
                            message: data.message,
                            type: data.type
                        }))
                        navigate('/profile')
                    } else {
                        console.log('erreur inconnue !');
                    }
                }).catch(error => {
                    console.log(error);

                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! reéssayer plus tard...";
                    if (!type)
                        type = "error";

                    dispatch(setNotification({
                        visible: true,
                        message: message,
                        type,
                    }))
                    setLoaded(false)
                })
        } else {
            if (theme.length === 0) setThemeMessage("le theme est obligatoire !")
            else if (theme.length < 15) setThemeMessage("le theme doit avoir plus de 15 caractérre !")
            if (theme.length >= 15) setThemeMessage("");

            !!pdf === false && setPdf(undefined)
            !!docx === false && setDocx(undefined)
        }
    }

    const handleCheck = (e: any): void => {
        const state = e.target.checked
        setIsChecked(state)
        setPdf(undefined)
        setDocx(undefined)
        refPdf.current.value = null;
        refDocx.current.value = null;
    }

    return (
        <div className='edit_book'>
            {!mobile ? <>
                <TopBar currentPage="Modifier" />
                <div className='card_option animated animatedFadeInUp fadeInUp'
                    style={{
                        background: `linear-gradient(
                        to top, 
                        rgb(0 0 0 / .9), rgb(0 0 0 / 0)
                    ), url(/${process.env.PUBLIC_URL}images/amis-apprenant-dans-groupe-etude_23-2149257178.jpg)`,
                        backgroundPositionY: "30%",
                        animationDelay: "200ms"
                    }}
                >
                    <div className="text_content">
                        <p className='short'>
                            <i style={{ color: "#fff" }} className='fa fa-book'></i> Vous avez fait une bourde ?
                        </p>
                        <p>Pas d'inquiétude, vous pouvez tout modifier en quelques cliques</p>
                    </div>
                </div>
            </> :
                <TopBarIllustration
                    title="Vous avez fait une bourde ?"
                    text="Pas d'inquiétude, vous pouvez tout modifier en quelques cliques"
                    url={`url(/${process.env.PUBLIC_URL}images/amis-apprenant-dans-groupe-etude_23-2149257178.jpg)`}
                />

            }
            <form
                className="add_book_form animated animatedFadeInUp fadeInUp"
                onSubmit={handleSubmitBook}
                encType="multipart/form-data"
            >
                <LoadBox isLoading={loaded} />
                <div className="files_upload">
                    <div className="is_update_file">
                        <span><b>Modifier les fichiers ?</b></span>
                        <input
                            type="checkbox"
                            hidden
                            name="is_update_file"
                            id='is_update'
                            onChange={handleCheck}
                        />
                        <label htmlFor="is_update">
                            <div className="commutateur">{isChecked ? "Non" : "Oui"}</div>
                        </label>
                    </div>
                    <div className={isChecked ? "" : "disable"}>
                        <span>Document Pdf <sup>*</sup></span>
                        <label className="lable_file" htmlFor="pdfFile">
                            <i style={{ color: "var(--danger)" }} className="fa fa-file-pdf-o"></i>
                            <input
                                type="file"
                                hidden
                                id="pdfFile"
                                accept='application/pdf'
                                onChange={handlePdfFile}
                                name="pdf"
                                ref={refPdf}
                            />
                            <p>{pdf?.name || ".pdf"}</p>
                        </label>
                        <span className="help_message">{(pdf === undefined && isChecked) && "Selectionnez un document au format *PDF* s'il vous plait"}</span>
                        <span>Document Word <sup>*</sup></span>
                        <label className="lable_file" htmlFor="docxFile">
                            <i style={{ color: "var(--primary)" }} className="fa fa-file-word-o"></i>
                            <input
                                type="file"
                                hidden
                                id="docxFile"
                                accept='.doc, .docx'
                                onChange={handleDocxFile}
                                name='docx'
                                ref={refDocx}
                            />
                            <p>{docx?.name || ".doc, .docx"}</p>
                        </label>
                        <span className="help_message">{(docx === undefined && isChecked) && "Selectionnez un document *WORD* s'il vous plait"}</span>
                    </div>
                </div>
                <div className="field">
                    <h3>Information suplémentaire sur les document fournit</h3>
                    <label htmlFor="">
                        <span>Theme <sup>*</sup></span>
                        <input
                            type="text"
                            name='theme'
                            onChange={(e: any) => setTheme(e.target.value)}
                            value={theme}
                            placeholder="Le sujet de votre document"
                        />
                        <span className="help_message">{themeMessage}</span>
                    </label>

                    <LevelTag level={level} setLevel={setLevel} />

                    <label htmlFor="">
                        <span>Description</span>
                        <textarea
                            value={description}
                            name="description"
                            onChange={(e: any) => setDescription(e.target.value)}
                            placeholder='Décrivez en quelques lignes le thème de votre document'
                        ></textarea>
                    </label>
                    <div className='add_book_btn'>
                        <button
                            className='cancel'
                            onClick={() => navigate('/profile')}
                            type="button"
                        >
                            <i className="fa fa-close"></i> Annuler
                        </button>

                        <button
                            className='submit_book'
                            type="submit"
                        >
                            {!loaded ? "Enregistrer" : "Chargement..."}
                        </button>
                    </div>

                </div>
            </form>
        </div>
    )
}

export default EditBook
