import axios from 'axios'
import dayjs from 'dayjs'
import React, { useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setNotification } from '../feature/notification.slice'
import { Level, UserSession } from '../_type'
import LevelTag from '../components/LevelTag'
import LoadBox from '../components/LoadBox'
import { useNavigate } from 'react-router-dom'
import TopBarIllustration from '../components/TopBarIllustration'
import TopBar from '../components/TopBar'


const AddBook = () => {
    const navigate = useNavigate()

    const [pdf, setPdf] = useState<any>(undefined)
    const [docx, setDocx] = useState<any>(undefined)
    const [theme, setTheme] = useState<string>("")
    const [themeMessage, setThemeMessage] = useState<string>("")
    const [loaded, setLoaded] = useState<boolean>(false)
    const [level, setLevel] = useState<Level>({ _id: "1", level: 2, value: "BTS" })

    const mobile: boolean = window.matchMedia("(max-width: 950px)").matches

    const user: UserSession = useSelector((store: any) => store.session)


    const alertBox = useRef<any>()
    const dispatch = useDispatch()

    const handlePdfFile = (e: any): void => {
        const file = e.target.files[0]
        if (!file) return
        if (file.type === "application/pdf") {
            setPdf(file)
        } else {
            setPdf(false)
        }
    }

    const handleDocxFile = (e: any): void => {
        const file = e.target.files[0]
        if (!file) return
        const isDocx: boolean = file.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
        const isDoc: boolean = file.type === "application/msword";
        if (isDoc || isDocx) {
            setDocx(file)
        } else {
            setDocx(false)
        }
    }

    const handleSubmitBook = (e: any): void => {
        e.preventDefault()
        const isLevel: boolean = !!level.level && !!level.value
        if (isLevel && theme.length > 15 && !!pdf && !!docx) {
            setThemeMessage("");
            setLoaded(true)

            const formdata: FormData = new FormData(e.currentTarget);

            formdata.append('_idUser', user.userId)
            formdata.append('level_value', level.value)
            formdata.append('level', level.level.toString())
            formdata.append('year', dayjs().format("YYYY"))
            formdata.append('option', user.speciality)
            axios.post(`${process.env.REACT_APP_API_URL}/book`, formdata)
                .then((res: any) => {
                    const { data } = res
                    dispatch(setNotification({
                        visible: true,
                        message: data.message,
                        type: data.type
                    }))
                    setLoaded(false)
                    if (data.type === "success") {
                        navigate('/profile')
                    }

                }).catch(error => {
                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! reéssayer plus tard...";
                    if (!type)
                        type = "error";

                    const color = `var(--${type === "error" ? "danger" : type})`
                    const { style, firstChild } = alertBox.current
                    style.display = "block"
                    firstChild.innerText = message
                    style.borderColor = color
                    // style.boxShadow = "0 0 50px var(--border)"
                    setLoaded(false)
                })
        } else {
            if (theme.length === 0) setThemeMessage("le theme est obligatoire !")
            else if (theme.length < 15) setThemeMessage("le theme doit avoir plus de 15 caractérre !")
            if (theme.length >= 15) setThemeMessage("");

            !!pdf === false && setPdf(false)
            !!docx === false && setDocx(false)
        }
    }

    return (
        <div className='add_book'>
            {!mobile ? <>
                <TopBar currentPage="Ajouter" />
                <div className='card_option animated animatedFadeInUp fadeInUp'
                    style={{
                        background: `linear-gradient(
                        to top, 
                        rgb(0 0 0 / .9), rgb(0 0 0 / 0)
                    ), url(/${process.env.PUBLIC_URL}images/add_book.jpg)`,
                        backgroundPositionY: "60%",
                        animationDelay: "200ms"
                    }}
                >
                    <div className="text_content">
                        <p className='short'>
                            <i style={{ color: "#fff" }} className='fa fa-book'></i> Premiér pas
                        </p>
                        <p>Vous y êtes presque</p>
                    </div>
                </div>
            </> :
                <TopBarIllustration
                    title="Premiér pas"
                    text="Vous y êtes presque"
                    url={`url(/${process.env.PUBLIC_URL}images/add_book.jpg)`}
                />

            }
            <form
                className="add_book_form animated animatedFadeInUp fadeInUp"
                onSubmit={handleSubmitBook}
                encType="multipart/form-data"
            >
                <div className="alert_box" ref={alertBox}>
                    <p></p>
                    <i
                        onClick={() => alertBox.current.style.display = "none"}
                        className='fa fa-close'
                    ></i>
                </div>
                <LoadBox isLoading={loaded} />
                <div className="files_upload">
                    <h3>Importer les documents Word et Pdf </h3>
                    <span>Document Pdf <sup>*</sup></span>
                    <label className="lable_file" htmlFor="pdfFile">
                        <i style={{ color: "var(--danger)" }} className="fa fa-file-pdf-o"></i>
                        <input
                            type="file"
                            hidden
                            id="pdfFile"
                            accept='application/pdf'
                            onChange={handlePdfFile}
                            name="pdf"
                        />
                        <p>{pdf?.name || ".pdf"}</p>
                    </label>
                    <span className="help_message">{pdf === false && "Selectionnez un document au format *PDF* s'il vous plait"}</span>
                    <span>Document Word <sup>*</sup></span>
                    <label className="lable_file" htmlFor="docxFile">
                        <i style={{ color: "var(--primary)" }} className="fa fa-file-word-o"></i>
                        <input
                            type="file"
                            hidden
                            id="docxFile"
                            accept='.doc, .docx'
                            onChange={handleDocxFile}
                            name='docx'
                        />
                        <p>{docx?.name || ".doc, .docx"}</p>
                    </label>
                    <span className="help_message">{docx === false && "Selectionnez un document *WORD* s'il vous plait"}</span>
                </div>
                <div className="field">
                    <h3>Information suplémentaire sur le document fournit</h3>
                    <label htmlFor="">
                        <span>Thème <sup>*</sup></span>
                        <input
                            type="text"
                            name='theme'
                            onChange={(e: any) => setTheme(e.target.value)}
                            value={theme}
                            placeholder="Le sujet de votre document"
                        />
                        <span className="help_message">{themeMessage}</span>
                    </label>

                    <LevelTag level={level} setLevel={setLevel} />

                    <label htmlFor="">
                        <span>Description</span>
                        <textarea name='description' placeholder='Décrivez en quelques lignes le thème de votre document'></textarea>
                    </label>
                    <div className='add_book_btn'>
                        <button
                            className='cancel'
                            onClick={() => navigate('/profile')}
                            type="button"
                        >
                            <i className="fa fa-close"></i> Annuler
                        </button>

                        <button
                            className='submit_book'
                            type="submit"
                        >
                            {!loaded ? "Envoyer le document" : "Chargement..."}
                        </button>
                    </div>

                </div>
            </form>
        </div>
    )
}

export default AddBook
