import React, { useState } from 'react'
import InputPassword from './InputPassword';
import axios from "axios"
import { useDispatch } from 'react-redux';
import { setNotification } from '../feature/notification.slice';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';
import { connectUser } from '../feature/session.slice';
import LoadBox from './LoadBox';
import { UserSession } from '../_type';



const SignInForm = (props: any) => {

    const [email, setEmail] = useState(props.emailLog)
    const [password, setPassword] = useState("")
    const [loaded, setLoaded] = useState(false)

    // const motification = useSelector((state: any) => state.notification)
    const dispatch: any = useDispatch();

    const navigate = useNavigate()

    const userData: { email: string, password: string } = {
        email: email,
        password: password,
    }

    const handlePassword: any = (e: any) => {
        setPassword(e.target.value);
    }

    const isEmail: any = () => {
        let element: any = document.getElementById("email") as HTMLInputElement
        let error: any = document.getElementById("email_error") as HTMLInputElement
        let regex: RegExp = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/

        if (!regex.test(email)) {
            element.style.borderColor = "var(--secondary)"
            error.style.display = "block"
            return false
        } else {
            error.style.display = "none"
            element.style.borderColor = "var(--green)"
        }
        return true
    }
    const handleSignIn: any = (e: any) => {
        e.preventDefault()
        if (isEmail()) {
            setLoaded(true)
            axios.post(`${process.env.REACT_APP_API_URL}/users/sign-in`, userData)
                .then(res => {
                    const userSession: UserSession = res.data.data
                    userSession.connected = true;
                    userSession.isAdmin = 1;

                    Cookies.set("token", userSession.token, { expires: 1 /*day */ })
                    Cookies.set("userId", userSession.userId, { expires: 1 /*day */ })

                    dispatch(setNotification({
                        visible: true,
                        message: res.data.message,
                        type: "success"
                    }))
                    dispatch(connectUser(userSession))
                    setEmail("")
                    setPassword("")
                    navigate("/profile")
                    setLoaded(false)
                })
                .catch(error => {
                    setLoaded(false)
                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne répond pas! reéssayer plus tard...";
                    if (!type)
                        type = "error";
                    dispatch(setNotification({
                        visible: true,
                        message: message,
                        type,
                    }))
                })
        }
    }
    return (
        <div className="login_form">
            <LoadBox isLoading={loaded} />
            <form
                className="animated animatedFadeInUp fadeInUp"
            >
                <input
                    type="email"
                    placeholder='Email'
                    value={email}
                    onChange={(e: any) => setEmail(e.target.value)}
                    id="email"
                />
                <span id='email_error'>Email invalide !</span>
                <InputPassword
                    password={password}
                    handlePassword={handlePassword}
                    placeholder="Mot de passe"
                />
                <span id='password_error'>8 charactères minimum pour le mot de passe !</span>
                {/* <a href='/' className='default_link forget_pwd'>Mot de passe oublié ?</a> */}
                <button
                    type="submit"
                    onClick={handleSignIn}
                    style={{ marginTop: 10 }}

                >
                    {!loaded ? "Se connecter" : "Chargement..."}
                </button>
            </form>
        </div>
    )
}

export default SignInForm
