import dayjs from 'dayjs';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setPeriode } from '../feature/periode.slice';
import Loader from './Loader';

const DropTags = (props: any) => {
    const { type, libele, open, icon, isLoading } = props
    const [hide, setHide] = useState(open)

    const stateByType: any = useSelector((store: any) => store.periode[type])

    interface datasList {
        id?: string,
        value: string,
    }
    const datas: Array<datasList> = props.datas

    const dispatch: any = useDispatch()
    const showOption: any = () => {
        hide ? setHide(false) : setHide(true)
    }
    const choiseTags = (ev: any) => {
        const classSelected: any = ev.target.classList;
        let key: string = ev.target.dataset.key
        if (classSelected.length > 1) {
            if (type === "year") key = dayjs().format("YYYY")
            else key = ""
        }

        switch (type) {
            case "year": dispatch(setPeriode({ year: key.toLowerCase() }))
                break
            case "option": dispatch(setPeriode({ option: key.toLowerCase() }))

                break
            case "level_value": dispatch(setPeriode({ level_value: key.toLowerCase() }))
                break
        }
    }

    return (
        <div className='drop_tabs'>
            <div className="title_bar" onClick={showOption}>
                <i className={icon}></i>
                <p>{libele}</p>
                <i
                    className={`fa fa-angle-down ${hide && "rotate"}`}
                    id="arrow"
                ></i>
            </div>
            <div className={!hide ? "content hide" : "content"}>
                {!isLoading ? datas.map((data: any, key: any) => <div
                    style={{ animationDuration: `${2 + key}00ms` }}
                    onClick={choiseTags}
                    data-key={data.value}
                    className={stateByType.toLowerCase() === data.value.toString().toLowerCase() ? "item selected" : "item"}
                    key={key}
                >
                    {data.short || data.value}
                </div>) : <Loader width={40} isLoading={isLoading} />}
            </div>
        </div>
    );
};

export default DropTags;