import React from 'react'
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const SkeletonCard = (props: any) => {
    return (

        <div
            className="book_card animated animatedFadeInUp fadeIn"
            style={{ animationDelay: `${2 + props.count}00ms`, pointerEvents: "none" }}
        >
            <a
                href='/'
            >
                <SkeletonTheme
                    baseColor="var(--skeleton-base-color)"
                    highlightColor="var(--skeleton-highlight-color)"
                >
                    <Skeleton
                        height={200}
                        width={"100%"}
                    />
                </SkeletonTheme>
            </a>
            <div className="text-content">
                <h3 className="">
                    <a href='/'>
                        <span>
                            <SkeletonTheme
                                baseColor="var(--skeleton-base-color)"
                                highlightColor="var(--skeleton-highlight-color)"
                            >
                                <Skeleton
                                    height={30}
                                    width={"70%"}
                                />
                            </SkeletonTheme>
                        </span>
                    </a>
                </h3>

                <div className="info">
                    <SkeletonTheme
                        baseColor="var(--skeleton-base-color)"
                        highlightColor="var(--skeleton-highlight-color)"
                    >
                        <Skeleton
                            height={15}
                            width={"50%"}
                        />
                    </SkeletonTheme>
                </div>

                <p className="description">
                    <SkeletonTheme
                        baseColor="var(--skeleton-base-color)"
                        highlightColor="var(--skeleton-highlight-color)"
                    >
                        <Skeleton
                            count={4}
                            height={20}
                            width={"100%"}
                        />
                    </SkeletonTheme>
                </p>

                <p className='read_more'>
                    <a href='/'>
                        <SkeletonTheme
                            baseColor="var(--skeleton-base-color)"
                            highlightColor="var(--skeleton-highlight-color)"
                        >
                            <Skeleton
                                height={15}
                                width={"20%"}
                            />
                        </SkeletonTheme>
                    </a>
                </p>
            </div>
        </div>
    )
}

export default SkeletonCard
