
import React, { useState } from 'react';
import { darkTheme, lightTheme } from '../_theme';
// import SkeletonIMG from './SkeletonIMG';


function ThemeSelector() {
    const [theme, setTheme] = useState<string>(localStorage.getItem("theme") || "")
    const switchTheme = (e: any) => {
        const choise: string = e.target.dataset.theme;
        if (choise === "light") {
            lightTheme()
            setTheme("light")
        }
        else if (choise === "dark") {
            darkTheme()
            setTheme("dark")
        }
    }
    return (
        <div className="theme_selector">
            <img
                style={{ borderColor: theme === "light" ? "var(--primary)" : "transparent" }}
                onClick={(e) => switchTheme(e)}
                data-theme='light'
                className='animated animatedFadeInUp fadeInUp'
                src={`${process.env.PUBLIC_URL}images/theme-light.jpg`}
                alt="settings"
            />
            <img
                style={{ borderColor: theme === "dark" ? "var(--primary)" : "transparent" }}
                onClick={(e) => switchTheme(e)}
                data-theme='dark'
                className='animated animatedFadeInUp fadeInUp'
                src={`${process.env.PUBLIC_URL}images/theme-dark.jpg`}
                alt="settings"
            />
        </div>
    );
}

export default ThemeSelector;