import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const SkeletonUserCard = (props: any) => {

    return (
        <div className="user_card animated animatedFadeInUp fadeIn  ">

            <a href='#lightbox' className="image" style={{ boxShadow: "none" }}>
                <SkeletonTheme
                    baseColor="var(--skeleton-base-color)"
                    highlightColor="var(--skeleton-highlight-color)"
                >
                    <Skeleton
                        height={150}
                        width={"100%"}
                    />
                </SkeletonTheme>
            </a>
            <div className="text-content">
                <h3>
                    <SkeletonTheme
                        baseColor="var(--skeleton-base-color)"
                        highlightColor="var(--skeleton-highlight-color)"
                    >
                        <Skeleton
                            height={20}
                            width={"60%"}
                        />
                    </SkeletonTheme>
                </h3>

                <div className="description">
                    <SkeletonTheme
                        baseColor="var(--skeleton-base-color)"
                        highlightColor="var(--skeleton-highlight-color)"
                    >
                        <Skeleton
                            height={80}
                            width={"100%"}
                        />
                    </SkeletonTheme>
                </div>

            </div>
        </div>
    )
}

export default SkeletonUserCard
