import React, { useState } from "react";
import { SpecialityModel } from "../_type";


interface Props {
    options: SpecialityModel[],
    setSpeciality?: any,
    id: string
}

const SelectCustom: React.FC<Props> = ({ options, setSpeciality, id }) => {
    const [searchTerm, setSearchTerm] = useState("");
    const [focus, setFocus] = useState(false);
    const filteredOptions = options.filter((option) =>
        option.value.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const handleChange = (e: any) => {
        const value = e.currentTarget.dataset.option
        setSearchTerm(value)
        setSpeciality(value)
        setFocus(false)
    };

    const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
        setSearchTerm(event.target.value);
    };

    return (
        <div className="select_custom">
            <input
                onFocus={() => setFocus(true)}
                onBlur={() => setTimeout(() => {
                    setFocus(false)
                }, 1000)}
                type="text"
                value={searchTerm}
                onChange={handleSearch}
                placeholder="Rechercher..."
                id={id}
            />
            <button type='button'>
                <i
                    style={{ color: "var(--primary)", fontSize: 20 }}
                    className='fa fa-angle-down'
                ></i>
            </button>
            <div className={focus ? "options" : "options hide"}>
                {filteredOptions.map((option: SpecialityModel, key: number) => (
                    <div
                        onClick={handleChange}
                        data-option={option.value}
                        className="option"
                        key={key}
                    >
                        {option.value}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default SelectCustom;
