import React, { useState, useEffect } from 'react'
import SearchBar from './SearchBar'
import DropTags from './DropTags'
import axios from 'axios'
import FilterPeriode from './FilterPeriode'
import { Level } from '../_type'
import dayjs from 'dayjs'
import { useDispatch, useSelector } from 'react-redux'
import { setAsideMenuToggle } from '../feature/global.slice'

const AsideBar = () => {
    const btnSearch = document.getElementById('btnSearch') as HTMLElement
    const [option, setOption] = useState([])
    const [year, setYear] = useState([
        { value: dayjs().subtract(1, "year").format("YYYY") },
        { value: dayjs().format("YYYY") }
    ])
    const [level, setLevel] = useState<Level[]>([
        { _id: "1", level: 2, value: "BTS" },
        { _id: "1", level: 3, value: "Licence" },
        { _id: "1", level: 5, value: "Master" }
    ])
    const [isLoading, setLoading] = useState(false)
    // const [thumb, setThumb] = useState(null) as any
    const dispatch = useDispatch()

    const { asideMenuToggle } = useSelector((store: any) => store.global)

    const handleScroll: any = (e: any) => {
        // console.log(e.currentTarget);
    }

    // define a function that sets min-height of my-element to window.innerHeight:
    const setHeight = () => {
        const menu = document.getElementById("aside_bar")
        if (menu) menu.style.height = window.innerHeight + "px"
    };

    // define mobile screen size:
    let deviceWidth = window.matchMedia("(max-width: 1024px)");

    if (deviceWidth.matches) {
        // set an event listener that detects when innerHeight changes:
        window.addEventListener("resize", setHeight);

        // call the function once to set initial height:
        setHeight();
    }



    useEffect(() => {
        setLoading(true)
        axios.get(`${process.env.REACT_APP_API_URL}/option`).then((res: any) => {
            const { data } = res.data
            if (data.length !== 0) {
                setOption(data)
            }
            setLoading(false)
        }).catch((error: any) => {
            console.log(error);
            setLoading(false)
        })

        axios.get(`${process.env.REACT_APP_API_URL}/year`).then((res: any) => {
            const { data } = res.data
            if (data.length !== 0) {
                setYear(data.sort((a: any, b: any) => a.value - b.value))
            }

        }).catch((error: any) => {
            console.log(error);
        })

        axios.get(`${process.env.REACT_APP_API_URL}/level`).then((res: any) => {
            const { data } = res.data
            if (data.length !== 0) {
                setLevel(data)
            }

        }).catch((error: any) => {
            console.log(error);
        })

        // const getThumbnail = async () => {
        //   const CoreControls = window.CoreControls;
        //   CoreControls.setWorkersPath('./webviewer/lib/core')
        // }

    }, [])


    return (
        <aside
            onClick={handleScroll}
            id="aside_bar"
            className={asideMenuToggle ? "close" : ""}
        >
            <div
                className="overlay"
                onClick={() => dispatch(setAsideMenuToggle({ asideMenuToggle: true }))}
                onTouchMove={() => dispatch(setAsideMenuToggle({ asideMenuToggle: true }))}
            ></div>
            <div className="mobile_top_aside" onClick={() => dispatch(setAsideMenuToggle({ asideMenuToggle: !asideMenuToggle }))}>
                <i style={{ fontSize: 18 }} className={asideMenuToggle ? "fa fa-sliders" : "fa fa-close"}></i>
            </div>
            <div className="content">
                <SearchBar />
                <FilterPeriode />
                <DropTags
                    libele="Cilcle"
                    datas={level}
                    type="level_value"
                    open={true}
                    icon="fa fa-tags"
                    isLoading={false}
                />
                <DropTags
                    libele="Années"
                    datas={year}
                    type="year"
                    open={true}
                    icon="fa fa-calendar"
                    isLoading={isLoading}
                />
                <DropTags
                    libele="Spécialité"
                    datas={option}
                    type="option"
                    open={true}
                    icon="fa fa-bookmark"
                    isLoading={isLoading}
                />
            </div>
            <div className="submit_filter" onClick={() => btnSearch.click()}>
                valider
                <i className='fa fa-check'></i>
            </div>
        </aside>
    )
}

export default AsideBar
