const darkTheme = () => {
  document.documentElement.style.setProperty("--background", "#3b3b3b");
  document.documentElement.style.setProperty("--accent", "#27282f");
  document.documentElement.style.setProperty("--input-accent", "#343434");
  document.documentElement.style.setProperty("--border", "#1c1c1c");
  document.documentElement.style.setProperty("--shadow", "#1c1c1cc7");
  document.documentElement.style.setProperty("--writing", "#e9e9e9");
  document.documentElement.style.setProperty("--danger", "#ff5757");

  document.documentElement.style.setProperty(
    "--skeleton-base-color",
    "#363636"
  );
  document.documentElement.style.setProperty(
    "--skeleton-highlight-color",
    "#404040"
  );

  localStorage.setItem("theme", "dark");
};
const lightTheme = () => {
  document.documentElement.style.setProperty("--background", "#ffffff");
  document.documentElement.style.setProperty("--accent", "#f1f1f1");
  document.documentElement.style.setProperty("--input-accent", "#ffffff");
  document.documentElement.style.setProperty("--border", "#e6e6e6");
  document.documentElement.style.setProperty("--shadow", "#aaaaaa8f");
  document.documentElement.style.setProperty("--writing", "#131523");
  document.documentElement.style.setProperty("--danger", "#c7192e");

  document.documentElement.style.setProperty(
    "--skeleton-base-color",
    "#e5e5e5"
  );
  document.documentElement.style.setProperty(
    "--skeleton-highlight-color",
    "#f5f5f5"
  );

  localStorage.setItem("theme", "light");
};

export { lightTheme, darkTheme };
