const { createSlice } = require("@reduxjs/toolkit");
const dayjs = require("dayjs");

let currentYear: string | null = localStorage.getItem("year");
if (!currentYear) {
  currentYear = dayjs().subtract(1, "year").format("YYYY");
}
let currentOption: string | null = localStorage.getItem("option");
let CurrentLevel: string | null = localStorage.getItem("level_value");

export const periodeSlice = createSlice({
  name: "periode",
  initialState: {
    year: currentYear,
    option: currentOption || "",
    level_value: CurrentLevel || "",
    page: 1,
    search: "tous",
  },

  reducers: {
    setPeriode: (state: any, action: any) => {
      // {type: "periode/setPeriode" payload: 1}

      const { year, option, level_value, search, page } = action.payload;
      if (year || year === "") {
        state.year = year;
        localStorage.setItem("year", year);
        localStorage.setItem("option", state.option);
        localStorage.setItem("level_value", state.level_value);
        state.page = 1;
      } else if (option || option === "") {
        state.option = option;
        localStorage.setItem("option", option);
        localStorage.setItem("year", state.year);
        localStorage.setItem("level_value", state.level_value);
        state.page = 1;
      } else if (level_value || level_value === "") {
        state.level_value = level_value;
        localStorage.setItem("level_value", level_value);
        localStorage.setItem("option", state.option);
        localStorage.setItem("year", state.year);
        state.page = 1;
      } else if (search || search === "") {
        state.search = search;
        localStorage.setItem("option", state.option);
        localStorage.setItem("year", state.year);
        localStorage.setItem("level_value", state.level_value);
        state.page = 1;
      } else if (page) {
        state.page = page;
      }
    },
  },
});

export const { setPeriode } = periodeSlice.actions;
