import React, { } from 'react'
import { useDispatch } from 'react-redux'
import Portal from './Portal'
import { setNotification } from '../feature/notification.slice'

const GetAccessBook = (props: any) => {
  const { visible, setVisible, contact } = props

  const dispatch = useDispatch()

  const code = [
    "GLO3244",
    "GLO6999",
    "GLO3225",
    "GLO3493",
    "GLO3444",
  ]

  const handleMatricule = (e: any) => {
    e.preventDefault()
    const codeEl: HTMLInputElement = document.getElementById('code') as HTMLInputElement
    const span: HTMLInputElement = document.getElementById('matricule_error') as HTMLInputElement
    const matriculeBox: HTMLInputElement = document.getElementById('matriculeBox') as HTMLInputElement

    const value: string = codeEl.value.toUpperCase().trim()
    let test: boolean = code.includes(value)
    if (test) {
      // setIsMatricule(true)
      codeEl.style.border = "1px solid var(--border)";
      matriculeBox.style.border = "1px solid var(--border)";
      span.textContent = ""
      dispatch(setNotification({
        type: "success",
        message: "Code d'accès valide. Le téléchargement vas commencer dans un instant !",
        visible: true
      }))
    } else {
      codeEl.style.border = "1px solid var(--danger)";
      matriculeBox.style.border = "1px solid var(--danger)";
      span.textContent = "Le code n'est pas valide. contactez le propriétaire du documment !"
    }
  }

  return (
    <>
      {
        (visible) &&
        <Portal>
          <div className="get_access_book">
            <form
              onSubmit={handleMatricule}
              id='matriculeBox'
              className="access_book animated animatedFadeInUp fadeInUp"
            >
              <i
                className='fa fa-close'
                title='annuler'
                onClick={() => setVisible(false)}
              ></i>
              <h3>Entrer le code d'accés</h3>
              <label htmlFor="code">
                <input
                  autoFocus
                  type="text"
                  name='code'
                  placeholder='code propriétaire'
                  id='code'
                />
                <span id='matricule_error'></span>
              </label>
              <button
                type='submit'
              >valider</button>
              <a
                target='_blank'
                rel="noopener noreferrer"
                href={`https://wa.me/237${contact}?text=internship-report:%20je%20souhaite%20utiliser%20votre%20rapport%20de%20stage`}
                title='connectez le propriétaire !'
                className='default_link'
              ><i className='fa fa-whatsapp'></i> connectez le propriétaire !</a>
            </form>
          </div>
        </Portal>
      }
    </>)
}


export default GetAccessBook

