import Cookies from 'js-cookie';
import React from 'react'
import { NavLink } from 'react-router-dom';



const Card = (props: any) => {
    let { thumbnail, theme, option, level_value, year, description, _id, _idUser } = props.book;
    const page = window.location.pathname;

    const user = Cookies.get("userId")
    const isMyCard: boolean = user === _idUser
    let thumbUrl = `url("${process.env.REACT_APP_API_URL}/uploads/${thumbnail}")`
    if (isMyCard) {
        thumbUrl = `url("${process.env.REACT_APP_API_URL}/uploads/${thumbnail}?t=${new Date().getTime()}")`
    }
    return (
        <div
            className="book_card animated animatedFadeInUp fadeInUp"
            style={{ animationDelay: `${2 + props.count}00ms` }}
        >
            <NavLink
                to={`/detail/${_id}`}
                className="image"
                style={{
                    backgroundImage: thumbUrl
                }}
            >
                {isMyCard && <i className='fa fa-bookmark'></i>}
            </NavLink>
            <div className="text-content">
                <h3 className="">
                    <NavLink to={`/detail/${_id}`}><span dangerouslySetInnerHTML={{ __html: theme }}></span></NavLink>
                </h3>

                <div className="info">
                    <span style={{ opacity: .5 }}><i className="fa fa-bookmark-o"></i> {option} </span>
                    <span><i className="fa fa-folder-o"></i> {level_value} </span>
                    <span style={{ opacity: .5 }}><i className="fa fa-calendar"></i> {year}</span>
                </div>

                <p className="description">
                    {description}
                </p>

                <p className='read_more'>
                    {(isMyCard && page === "/profile") ? <NavLink to={`/edit_book/${_id}`}>
                        <button>
                            <i className='fa fa-pencil'></i>Modifier
                        </button>
                    </NavLink> :
                        <NavLink to={`/detail/${_id}`}>Afficher plus...</NavLink>}
                </p>
            </div>
        </div>
    )
}

export default Card
